import { Link } from '@reach/router';
import React, { Component } from 'react';
import './Header.css';

class Header extends Component {
  render() {
    return (
      <div className="Header">
        <header id="header" className="headerTop header headerBackground">
          <div className="container">
            <figure className="logoContainer">
              <Link className="logoLink externalMenu" to="/">
                <img src="https://drive.google.com/uc?id=1x8AjooD16XjYfABZW5SfDbN9d6NK275T" alt="" className="logoHeader" />
                <p className="logoText">Ashara Studios</p>
              </Link>
            </figure>
            <figure id="menu" className="menuBorder menuIcon" >
              <img id="bordeMenu" src="https://drive.google.com/uc?id=1T6fAa3OBfP1wOXKpAkLznjk8jSSqPADu" alt="" />
            </figure>
            <figure className="barrasContainer menuIcon">
              <img src="https://drive.google.com/uc?id=1iDZSKpZ2oHDCl81bW131b_17JlmA0ByU" alt="" />
            </figure>
            <input type="checkbox" id="cbxMenu" />
            <div className="modules">
              <ul>
                <li className="logoMenu">
                  <img src="https://drive.google.com/uc?id=1etH6wKHI6JBnKxydOeHoUDnpGG_EwnCq" alt="" />
                </li>
                <li>
                <nav className="headerDropDownMenu">
                  <input type="checkbox" className="headerActivateMenu" id="headerAccordion" name="headerAccordion" />
                  <p className="textMenu borderMenuBottom">
                    <label id="units" className="cursorPointer" htmlFor="headerAccordion">
                      UNITS
                      <span id="unitsIcon" className="unitsIcon">+</span>
                    </label>        
                  </p>
                  <div className="headerDropDown unitsDetails">
                    <div className="unitsLinksContainer">
                      <div className="units unitLink">
                        <Link className="link soft" to="/soft">SOFT</Link>
                      </div>
                      <div className="units unitLink">
                        <Link className="link science" to="/science">SCIENCE</Link>
                      </div>
                      <div className="units unitLink">
                        <Link className="link games" to="/games">GAMES</Link>
                      </div>
                      <div className="units unitLink">
                        <Link className="link iot" to="/iot">IOT</Link>
                      </div>
                      <div className="units unitLink">
                        <Link className="link school" to="/school">SCHOOL</Link>
                      </div>
                      <div className="units unitLink">
                        <Link className="link media" to="/media">MEDIA</Link>
                      </div>
                    </div>
                </div> 
                </nav>
                </li>
                <li>
                  <p id="aboutUs" className="units textMenu externalMenu">
                    <Link to="/aboutus">ABOUT US</Link>
                  </p>
                </li>
                <li>
                  <p id="contactUs" className="units textMenu borderMenuTop externalMenu">
                    <Link  to="/contactus">CONTACT US</Link>
                  </p>
                </li>
              </ul>
            </div>
          </div>
        </header>
      </div>
    );
  }
  //----->ANIMACIONES
  componentDidMount() {
    var menuMobile = document.getElementById('menu');
    var menu = document.getElementsByClassName('units');
    var externalMenu = document.getElementsByClassName('externalMenu');//headerAccordion
    var cbxHeaderAccordion = document.getElementById('headerAccordion');
    var cbxMenu = document.getElementById('cbxMenu');
    var bordeMenu = document.getElementById('bordeMenu');
    var units = document.getElementById('units');
    var unitsIcon = document.getElementById('unitsIcon');

    this.adicionarEventosMenu(menu, cbxMenu, bordeMenu, units, unitsIcon, externalMenu, cbxHeaderAccordion, menuMobile);
  };

  async adicionarEventosMenu(menu, cbxMenu, bordeMenu, units, unitsIcon, externalMenu, cbxHeaderAccordion, menuMobile) {
    if (units){
      units.addEventListener('click', () => {
        unitsIcon.innerHTML = (unitsIcon.innerHTML === "-") ? "+" : "-";
      });
    }

    if (externalMenu){
      for (let i = 0; i < externalMenu.length; i++) {
        externalMenu[i].addEventListener('click', () => {
          cbxHeaderAccordion.checked = false;
          unitsIcon.innerHTML = "+";
        });
      }
    }

    if (menuMobile){
      menuMobile.addEventListener('click', () => {
        if (window.innerWidth <= 768){
          cbxMenu.checked = !cbxMenu.checked;
          this.changeAnimationClass(cbxMenu, bordeMenu);
        }
      });

      menuMobile.addEventListener('animationend', () => {
        this.animateButtonMenuMobile(bordeMenu);
      });
    }

    if (menu) {
      for (let i = 0; i < menu.length; i++) {
        menu[i].addEventListener('click', () => {
          this.manageSelectedMenu(menu, i);
          window.scrollTo(0, 0);

          if (window.innerWidth <= 768){
            cbxMenu.checked = !cbxMenu.checked;
            this.changeAnimationClass(cbxMenu, bordeMenu);
          }
        });
        menu[i].addEventListener('animationend', () => {
          this.animateButtonMenuMobile(bordeMenu);
        });
      }
    }
  }

  changeAnimationClass(cbxMenu, bordeMenu) {
    cbxMenu.checked === true ? bordeMenu.classList.add('animateLeft') : bordeMenu.classList.add('animateRight');
  }

  manageSelectedMenu(menu, selectedMenu) {
    for (let i = 0; i < menu.length; i++) {
      menu[i].classList.remove('underline');
    }

    menu[selectedMenu].classList.add('underline');
  }

  animateButtonMenuMobile(bordeMenu){
    if (window.innerWidth <= 768){
      bordeMenu.classList.remove('animateRight', 'animateLeft');
    }
  }
  //-----<ANIMACIONES

}

export default Header;