import React, { Component } from 'react';
import './FooterIcon.css';

class FooterIcon extends Component {
  render() {
    return (
      <div className="icon-container">
        <img alt="footer-icon" className="footer-icon footer-icon-small" src={this.props.smallIcon} />
        <img alt="footer-icon" className="footer-icon footer-icon-medium" src={this.props.medIcon} />
        <img alt="footer-icon" className="footer-icon footer-icon-big" src={this.props.bigIcon} />
        <img alt="footer-icon" className="footer-icon footer-icon-large" src={this.props.largeIcon} />
      </div>
    );
  }
}

export default FooterIcon;